import {
  getBrandList,
  getMakeList
} from '../../api/index.js';
let state = {
  letters: [],
  brand: {},
  letter: '',     // 
  isShow: false,  // 
  brandId: '', //id
  makeList: '', // 
  isShowList: false
}

let mutations = {
  initState: (state, payload) => {
    // 
    let letters = [];
    let brand = {};
    payload.forEach((item) => {

      let spell = item.Spelling.slice(0, 1); //
      //   console.log(spell)
      if (letters.indexOf(spell) == -1) {
        // letters  
        letters.push(spell)
        // brand
        brand[spell] = [item];

      } else {
        brand[spell].push(item)
      }
    });

    state.letters = letters;
    state.brand = brand;
  },
  touchstart:()=>{
    state.isShowList = true;
  },
  touchend:()=>{
    state.isShowList = false;
  },
  changemake: (state, payload) => {
    state.brandId = payload.id;
    state.makeList = payload.list
    state.isShowList = true;
    // console.log(state.isShowList)
    // state.isShowList=true
  },
  // 
  showLetter: (state, payload)=>{
    state.isShow = payload;
},
// letter
changeLetter: (state, payload)=>{
    state.letter = payload;
},
  hideMakeList: (state, payload)=> {
    state.isShowList = false
  }

}
let actions = {
  initState: ({
    commit
  }, payload) => {
    getBrandList().then(body => {
      if (body.code == 1) {
        commit('initState', body.data);
      } else {
        alert(body.msg);
      }
    })
  },
  getMakeList: ({
    commit
  }, payload) => {
    // console.log(payload)
    getMakeList(payload).then(body => {
      // console.log(body.data)
      commit('changemake', {
        id: payload,
        list: body.data
      })
    })
  }

}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



