import {
  getCityList
} from '../../api'
let state = {
  locationCity: '',
  provinces: [],
  cities: [],
  isShowCity: false

}
let mutations = {
  updataProvinces(state, payload) {
    state.provinces = payload
  },
  updataCities(state, payload) {
    state.cities = payload;
    state.isShowCity = true
  },
  hideCity(state, payload) {
    state.isShowCity = false
  }

}

let actions = {
  getCityList({
    commit
  }, payload) {
    // console.log(payload)
    // (id)
    if (payload) {
      getCityList(payload)
        .then(body => {
          commit('updataCities', body.data)
        })
    } else {
      getCityList({
          commit
        }, payload)
        .then(body => {
          commit('updataProvinces', body.data)
        })
    }

  }

}
export default {
  namespaced: true,
  state,
  mutations,
  actions
}



